import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import Pomodoro from "./Pomodoro";

class App extends Component {
  constructor() {
    super();
    this.state = {
      breakTime: "05",
      seshTime: "25",
      minutes: "25",
      seconds: "00",
      getMinutes: () => {
        this.setState(prevState => {
          if (this.state.minutes === "00" && this.state.seconds === "00") {
            alert("Time's up!");
            clearInterval(this.state.getMinutes);
            return {
              minutes: "00"
            };
          } else {
            return {
              minutes:
                Number(prevState.minutes) - 1 > 9
                  ? Number(prevState.minutes) - 1
                  : Number(prevState.minutes) === 0
                  ? "00"
                  : "0" + (Number(prevState.minutes) - 1)
            };
          }
        });
      },
      getSeconds: () => {
        if (
          this.state.minutes === this.state.seshTime &&
          this.state.seconds === "00"
        ) {
          this.setState(prevState => {
            return {
              minutes: Number(prevState.minutes) - 1,
              seconds: "60"
            };
          });
        }
        this.setState(prevState => {
          if (this.state.minutes === "00" && this.state.seconds === "00") {
            return {
              seconds: "59"
            };
          } else if (
            this.state.minutes === "00" &&
            this.state.seconds === "00"
          ) {
            clearInterval(this.state.getSeconds);
            return { seconds: "00" };
          } else if (
            this.state.minutes !== "00" &&
            this.state.seconds === "00"
          ) {
            return { seconds: "59" };
          } else {
            return {
              seconds:
                Number(prevState.seconds) - 1 > 9
                  ? Number(prevState.seconds) - 1
                  : Number(prevState.seconds) === 0
                  ? "00"
                  : "0" + (Number(prevState.seconds) - 1)
            };
          }
        });
      },
      counting: false
    };
    this.handleTime = this.handleTime.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.handleBreakIncrement = this.handleBreakIncrement.bind(this);
    this.handleBreakDecrement = this.handleBreakDecrement.bind(this);
    this.handleSeshIncrement = this.handleSeshIncrement.bind(this);
    this.handleSeshDecrement = this.handleSeshDecrement.bind(this);
  }
  handleTime() {
    if (this.state.counting === false) {
      setInterval(this.state.getMinutes, 60000);
      setInterval(this.state.getSeconds, 1000);
      this.setState({ counting: true });
    } else if (this.state.counting === true) {
      clearInterval(this.state.getMinutes);
      clearInterval(this.state.getSeconds);
      this.setState({ counting: false });
    }
  }

  handleReset() {
    clearInterval(this.state.getMinutes);
    clearInterval(this.state.getSeconds);
    this.setState({
      minutes:
        Number(this.state.seshTime) > 9
          ? this.state.seshTime
          : "0" + Number("0" + this.state.seshTime),
      seconds: "00"
    });
  }
  handleBreakIncrement() {
    this.setState(prevState => {
      return {
        breakTime:
          Number(prevState.breakTime) + 1 > 9
            ? Number(prevState.breakTime) + 1
            : "0" + (Number(prevState.breakTime) + 1)
      };
    });
    this.setState(prevState => {
      return {
        minutes: prevState.breakTime,
        seconds: "00"
      };
    });
  }
  handleBreakDecrement() {
    this.setState(prevState => {
      return {
        breakTime:
          Number(prevState.breakTime) - 1 > 9
            ? Number(prevState.breakTime) - 1
            : Number(prevState.breakTime) === 0
            ? "00"
            : "0" + (Number(prevState.breakTime) - 1)
      };
    });
    this.setState(prevState => {
      return {
        minutes: prevState.breakTime,
        seconds: "00"
      };
    });
  }
  handleSeshIncrement() {
    this.setState(prevState => {
      return {
        seshTime:
          Number(prevState.seshTime) + 1 > 9
            ? Number(prevState.seshTime) + 1
            : "0" + (Number(prevState.seshTime) + 1)
      };
    });
    this.setState(prevState => {
      return {
        minutes: prevState.seshTime,
        seconds: "00"
      };
    });
  }
  handleSeshDecrement() {
    this.setState(prevState => {
      return {
        seshTime:
          Number(prevState.seshTime) - 1 > 9
            ? Number(prevState.seshTime) - 1
            : Number(prevState.seshTime) === 0
            ? "00"
            : "0" + (Number(prevState.seshTime) - 1)
      };
    });
    this.setState(prevState => {
      return {
        minutes: prevState.seshTime,
        seconds: "00"
      };
    });
  }
  render() {
    return (
      <Pomodoro
        logo={logo}
        handleTime={this.handleTime}
        handleReset={this.handleReset}
        breakIncrement={this.handleBreakIncrement}
        breakTime={this.state.breakTime}
        breakDecrement={this.handleBreakDecrement}
        seshIncrement={this.handleSeshIncrement}
        seshTime={this.state.seshTime}
        seshDecrement={this.handleSeshDecrement}
        minutes={this.state.minutes}
        seconds={this.state.seconds}
      />
    );
  }
}

export default App;
