import React from 'react'

function Timer(props){

    return (
        <div className="flex-third" id="timer">

            <h3 id="timer-label">Session</h3>

            <div id="time-left">
            {props.minutes}:{props.seconds}
            </div>

            <div className="timer-buttons">
            <p id="start_stop" onClick={props.handleTime}>Start/Stop</p>
            <p id="reset" onClick={props.handleReset}>Reset</p>
            </div>

        </div>
    )
}

export default Timer