import React from 'react'
import Break from './Break'
import Timer from './Timer'
import Sesh from './Sesh'

function Pomodoro(props) {

    return (
        <div id="whole-app">

            <header id="Header">
                <img src={props.logo} className="App-logo" alt="logo" />
                <h1>Steven's Pomodoro Clock</h1>
            </header>

            <main id="Main">

                <Break
                    breakIncrement = {props.breakIncrement}
                    breakTime = {props.breakTime}
                    breakDecrement = {props.breakDecrement}
                />

                <Timer
                    handleTime = {props.handleTime}
                    handleReset = {props.handleReset}
                    minutes = {props.minutes}
                    seconds = {props.seconds}
                />

                <Sesh
                    seshIncrement = {props.seshIncrement}
                    seshTime = {props.seshTime}
                    seshDecrement = {props.seshDecrement}
                />

            </main>
        </div>
    );
}

export default Pomodoro